
































































import $ from "jquery"
import 'slick-carousel';
import 'slick-carousel/slick/slick.css';
import 'animate.css/animate.min.css';
import Ture from "@/components/Ture.vue";
import Vue from "vue"
import {Component} from 'vue-property-decorator';

@Component({
  name: "App",
  components: {
    Ture
  }
})

export default class App extends Vue {
  private burgerShow: boolean = false;

  mounted() {
    this.homeSlider();
    this.aboutSlider();


    $(window).scroll(() => {
      if (pageYOffset === 0) {
        $(".header").removeClass("scroll")
      } else {
        $(".header").addClass("scroll")
      }
    })
  }


  burgerMenuShow() {
    this.burgerShow = !this.burgerShow
  }

  homeSlider() {
    $(".home__sliders").slick({
      lazyLoad: "ondemand",
      arrows: false,
      dots: true,
      fade: true,
      infinite: true,
      autoplay: true,
      autoplaySpeed: 7000,
      speed: 1000,
      slidesToShow: 1,
      slidesToScroll: 1,
      pauseOnHover: false,
      "responsive": [
        {
          "breakpoint": 992,
          "settings": {
            dots: false
          }
        }
      ]
    })

    $('.home__sliders').on('beforeChange', function (event, slick, currentSlide, nextSlide) {
      let slide = $(".slider");
      slide.eq(nextSlide).children().addClass("animate__animated animate__fadeInUp");
      slide.eq(currentSlide).children().removeClass("animate__animated animate__fadeInUp");
    })
  }

  aboutSlider() {
    $(".about-me__awards").slick({
      slidesToShow: 4,
      lazyLoad: "ondemand",
      dots: false,
      infinite: true,
      speed: 1000,
      "responsive": [
        {
          "breakpoint": 1200,
          "settings": {
            slidesToShow: 3,
          }
        },
        {
          "breakpoint": 992,
          "settings": {
            slidesToShow: 2,
          }
        },
        {
          "breakpoint": 768,
          "settings": {
            slidesToShow: 1,
          }
        }
      ]
    })
  }

}

